import React from 'react';
import { Route, Redirect, Switch } from 'react-router-dom';
import { Login } from '../login/login';
import { Home } from '../home/home';
import { AddProduct } from '../addProduct/addProduct';
import {Belts} from '../fetchProduct/men/belts';
import {Jeans} from '../fetchProduct/men/jeans';
import {MenWatches} from '../fetchProduct/men/menWatches';
import {Shirts} from '../fetchProduct/men/shirts';
import {Shoes} from '../fetchProduct/men/shoes';
import {Tshirts} from '../fetchProduct/men/tshirts';
import {Handbags} from '../fetchProduct/women/handbags';
import {Jewellery} from '../fetchProduct/women/jewellery';
import {SalwarSuit} from '../fetchProduct/women/salwarsuit';
import {Sandals} from '../fetchProduct/women/sandals';
import {Sarees} from '../fetchProduct/women/sarees';
import {WomenWatches} from '../fetchProduct/women/womenWatches';
import { UpdateProduct } from '../updateProduct/updateProduct';
import { Contact } from '../contact/contact';

export const Routes = (props) => {
    
    return (
        <div>
            <Switch>
                <Route path="/" exact component={Login}/>

                <Route path="/home" exact component={Home}/>

                <Route path="/saveprod" exact component={AddProduct}/>

                 <Route path="/getmenshoes" exact component={Shoes}/>

                 <Route path="/getmenshirts" exact component={Shirts}/>

                 <Route path="/getmentshirts" exact component={Tshirts}/>

                 <Route path="/getmenjeans" exact component={Jeans}/>

                 <Route path="/getmenbelts" exact component={Belts}/>

                 <Route path="/getmenwatches" exact component={MenWatches}/>

                 <Route path="/getwomensarees" exact component={Sarees}/>

                 <Route path="/getwomenbags" exact component={Handbags}/>

                 <Route path="/getwomenjewellery" exact component={Jewellery}/>

                 <Route path="/getwomensandals" exact component={Sandals}/>

                 <Route path="/getwomensalwar" exact component={SalwarSuit}/>

                 <Route path="/getwomenwatches" exact component={WomenWatches}/>

                <Route path="/updateprod/:id" exact component={UpdateProduct}/>

                <Route path="/contactme" exact component={Contact}/>

                <Redirect exact to="/home"/>
            </Switch>
        </div>
    );
}