import React from 'react';
import axios from 'axios';
import '../content/css/login/login.css';
import icon from '../content/images/login/me_80x80.jpg';
import Swal from 'sweetalert2';

export class Login extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            username: '',
            password: ''
        };
    }

    onChange = (e) => {
		const state = this.state
		state[e.target.name] = e.target.value;
		this.setState(state);
    }
    
    onSubmit = (e) => {
		e.preventDefault();
        const { username, password } = this.state;
        if(username === 'tarun' && password === 'parve') {
           axios.post('http://localhost:9090/api/user/login', { username, password })
		  .then((result) => {
              localStorage.setItem('token', JSON.stringify(username + ':' + password));
              Swal({
                  title: 'Sign In!',
                  text: 'You Have Successfully Logged In',
                  type: 'success'
              });
              this.props.history.push("/home");
              console.log('You Have Logged In Successfully :: '+result);
		  });
        }
        else {
            Swal({
                title: 'Incorrect Username Or Password!',
                text: 'Unable To Logged In',
                type: 'error'
            });
            this.props.history.push("/");
            console.log("Unable To Logged In");
        }	
    }

    showPassword() {
        var x = document.getElementById("myPassword");
        if (x.type === "password") {
            x.type = "text";
        } else {
            x.type = "password";
        }
    }

    render() {

        const { username, password } = this.state;

        return (
<div>
    <h1 id="set_style">Tarun Shoppy Admin Login Form</h1>
    <br/>
    <div class="w3ls-login box box--big">
        <form onSubmit={this.onSubmit}>
            <img src={icon} alt="img" />
            <div class="agile-field-txt">
                <label>
                    <i class="fa fa-user" aria-hidden="true"></i> Username</label>
                <input type="text" name="username" onChange={this.onChange} value={username} placeholder="Enter Username" id="myUsername"/>
            </div>
            <div class="agile-field-txt">
                <label>
                    <i class="fa fa-lock" aria-hidden="true"></i> Password</label>
                <input type="password" name="password" onChange={this.onChange} value={password} placeholder="Enter Password" id="myPassword"/>
                <br/>
                <br/>
                <div class="agile_label">
                    <input id="check3" name="check3" type="checkbox" value="show password" onclick={this.showPassword.bind(this)}/>
                    <label class="check" for="check3">Show Password</label>
                </div>
            </div>
            <div class="w3ls-bot">
                <input type="submit" value="LOGIN" />
            </div>
        </form>
    </div>
    <div class="copy-wthree">
        <p id="setfooterLabel">
            Username&nbsp;&nbsp;
            <i class="fa fa-share" aria-hidden="true"></i>&nbsp;&nbsp;tarun&nbsp;&nbsp;&nbsp;&nbsp;Password&nbsp;&nbsp;
            <i class="fa fa-share" aria-hidden="true"></i>&nbsp;&nbsp;parve
        </p>
    </div>
</div>
        );
    }
}
